// ====================================
// Customize breadcrumbs
// ====================================

/*
* TABLE OF CONTENTS
*
* sprite image
* --- normal
* --- menu verticle
* --- control direction
* --- weather
* --- social
* --- soccer-calendar
* --- user and mobile
* LOGO + LIVE ICON +
* publisher icon
*/

// > LOGO + LIVE ICON + 
.logo-solid {
    background-image: url('../img/desktop-logo-1.0.0.png');
    display: inline-block;
    vertical-align: middle;
    background-size: 200px 66px;
    width: 200px;
    height: 66px;
}


.spr {
    background-image: url('../img/desktop-sprite-'+$sprite-version+'.png');
    background-size: ($sprite-w + px) ($sprite-h + px);
    display: inline-block;
    vertical-align: middle;
    /* > --- header */
    &--search {
        background-position: -0px -200px;
        width: 24px;
        height: 24px;
    }
    &--raylight {
        background-position: -0px -250px;
        width: 24px;
        height: 24px;
    }
    &--home {
        background-position: -0px -300px;
        width: 24px;
        height: 24px;
    }
    /* > --- social */
    &--fb {
        background-position: -0px -0px;
        width: 24px;
        height: 24px;
    }
    &--gplus {
        background-position: -0px -50px;
        width: 24px;
        height: 24px;
    }
    &--zalo {
        background-position: -0px -100px;
        width: 24px;
        height: 24px;
    }
    /* > --- story */
    &--comment {
        background-position: -0px -2100px;
        width: 16px;
        height: 16px;
    }
    .special-box &--comment {
        background-position: -0px -2150px;
    }
    .theme-black &--comment {
        background-position: -0px -2132px;
    }
    /* > --- box icon */
    
    &--box-eye{
        background-position: -0px -1200px;
        width: 32px;
        height: 32px;
    }
    &--box-comment{
        background-position: -0px -1250px;
        width: 32px;
        height: 32px;
    }
    &--box-more{
        background-position: -0px -1300px;
        width: 32px;
        height: 32px;
    }
    &--poll{
        background-position: -50px -1300px;
        width: 32px;
        height: 32px;
    }
    &--thinking{
        background-position: -0px -1350px;
        width: 24px;
        height: 24px;
    }
    /* > --- box icon */
    &--w-chanceflurries,
    &--w-chancesnow,
    &--w-flurries,
    &--w-snow,
    &--w-nt_chanceflurries,
    &--w-nt_chancesnow,
    &--w-nt_flurries,
    &--w-nt_snow,
    {
        background-position: -50px -2850px;
        width: 50px;
        height: 50px;
    }
    &--w-chancerain,
    &--w-rain,
    &--w-nt_chancerain,
    &--w-nt_rain,
    {
        background-position: -50px -2900px;
        width: 50px;
        height: 50px;
    }
    &--w-chancesleet,
    &--w-sleet,
    &--w-nt_chancesleet,
    &--w-nt_sleet,
    {
        background-position: -50px -2950px;
        width: 50px;
        height: 50px;
    }
    &--w-chancetstorms,
    &--w-tstorms,
    &--w-nt_chancetstorms,
    &--w-nt_tstorms,
    {
        background-position: -50px -3000px;
        width: 50px;
        height: 50px;
    }
    &--w-clear,
    &--w-sunny,
    {
        background-position: -50px -3050px;
        width: 50px;
        height: 50px;
    }
    &--w-nt_clear,
    &--w-nt_sunny,
    {
        background-position: -50px -3100px;
        width: 50px;
        height: 50px;
    }
    &--w-nt_mostlycloudy,
    &--w-nt_mostlysunny,
    &--w-nt_partlycloudy,
    &--w-nt_partlysunny,
    {
        background-position: -50px -3150px;
        width: 50px;
        height: 50px;
    }
    &--w-mostlycloudy,
    &--w-mostlysunny,
    &--w-partlycloudy,
    &--w-partlysunny,
    &--w-cloudy,
    &--w-windy {
        background-position: -50px -3200px;
        width: 50px;
        height: 50px;
    }

    &--w-fog,
    &--w-hazy,
    {
        background-position: -50px -3250px;
        width: 50px;
        height: 50px;
    }
    &--w-nt_fog,
    &--w-nt_hazy,
    &--w-nt_cloudy,
    {
        background-position: -50px -3250px;
        width: 50px;
        height: 50px;
    }
    &--w-chanceflurries-mini,
    &--w-chancesnow-mini,
    &--w-flurries-mini,
    &--w-snow-mini,
    &--w-nt_chanceflurries-mini,
    &--w-nt_chancesnow-mini,
    &--w-nt_flurries-mini,
    &--w-nt_snow-mini,
    {
        background-size: ($sprite-w *.7 + px) ($sprite-h *.7 + px);
        background-position: -35px (-2850px * 0.7);
        width: 35px;
        height: 35px;
    }
    &--w-chancerain-mini,
    &--w-rain-mini,
    &--w-nt_chancerain-mini,
    &--w-nt_rain-mini,
    {
        background-size: ($sprite-w *.7 + px) ($sprite-h *.7 + px);
        background-position: -35px (-2900px * 0.7);
        width: 35px;
        height: 35px;
    }
    &--w-chancesleet-mini,
    &--w-sleet-mini,
    &--w-nt_chancesleet-mini,
    &--w-nt_sleet-mini,
    {
        background-size: ($sprite-w *.7 + px) ($sprite-h *.7 + px);
        background-position: -35px (-2950px * 0.7);
        width: 35px;
        height: 35px;
    }
    &--w-chancetstorms-mini,
    &--w-tstorms-mini,
    &--w-nt_chancetstorms-mini,
    &--w-nt_tstorms-mini,
    {
        background-size: ($sprite-w *.7 + px) ($sprite-h *.7 + px);
        background-position: -35px (-3000px * 0.7);
        width: 35px;
        height: 35px;
    }
    &--w-clear-mini,
    &--w-sunny-mini,
    {
        background-size: ($sprite-w *.7 + px) ($sprite-h *.7 + px);
        background-position: -35px (-3050px * 0.7);
        width: 35px;
        height: 35px;
    }
    &--w-nt_mostlycloudy-mini,
    &--w-nt_mostlysunny-mini,
    &--w-nt_partlycloudy-mini,
    &--w-nt_partlysunny-mini,
    &--w-nt_cloudy-mini,
    {
        background-size: ($sprite-w *.7 + px) ($sprite-h *.7 + px);
        background-position: -35px (-3100px * 0.7);
        width: 35px;
        height: 35px;
    }
    &--w-mostlycloudy-mini,
    &--w-mostlysunny-mini,
    &--w-partlycloudy-mini,
    &--w-partlysunny-mini,
    &--w-windy-mini,
    &--w-cloudy-mini,
    {
        background-size: ($sprite-w *.7 + px) ($sprite-h *.7 + px);
        background-position: -35px (-3150px * 0.7);
        width: 35px;
        height: 35px;
    }
    &--w-fog-mini,
    &--w-hazy-mini,
    {
        background-size: ($sprite-w *.7 + px) ($sprite-h *.7 + px);
        background-position: -35px (-3200px * 0.7);
        width: 35px;
        height: 35px;
    }
    &--w-nt_fog-mini,
    &--w-nt_hazy-mini,
    {
        background-size: ($sprite-w *.7 + px) ($sprite-h *.7 + px);
        background-position: -35px (-3250px * 0.7);
        width: 35px;
        height: 35px;
    }
}
