// > main navigation 
.nav-wrap{
    height: 35px;
}
.nav {
    background-color: darken($first-color, 5%);
    height: 35px;
    left: 0;
    right: 0;
    z-index: 10;
    &__parent {
        float: left;
        height: 35px;
        > a {
            @include font( 14, 35,$normal);
            height: 35px;
            text-transform: uppercase;
            display: block;
            padding: 0px 6px 0 5px;
            color: #fff;
            cursor: pointer;
        }
        &:hover .nav__child {
            left: 30px;
            display: block;
            z-index: 2147483647;
        }
        &:hover,
        &.is-active{
            background-color: #fff;
            > a{
                color: $first-color;
            }
        }
        &:last-child:hover .nav__child ,
        &:nth-last-child(2):hover .nav__child ,
        &:nth-last-child(3):hover .nav__child {
            right: 0px;
            left: inherit;
            display: block;
            z-index: 2147483647;
        }
        &:first-child{
            > a{
                padding-left: 0;
            }
            i{
                vertical-align: top;
                margin-top: 5px;
            }
            &:hover{
                background-color: transparent;
            }
        }
    }
    &__child {
        position: absolute;
        left: -999em;
        width: 950px;
        background: #FFFFFF;
        box-shadow: 0 2px 10px 0 rgba(0,0,0,0.20);
        z-index: 10;
        padding: 15px;
        [class^="col-"] {
            float: left;
            margin-left: 15px;
        }
        .col-450{
            margin-left: 0;
        }
        .link{
            a{
                display: inline-block;
                height: 45px;
                @include font(14,45,$normal);
                padding: 0px 8px;
                &.is-active{
                    text-decoration: underline;
                }
                &:first-child{
                    padding-left: 0;
                }
            }
        }
        .content{
        }
        .col-450{
            .story{
            }
            .story__thumb{
                margin-bottom: 15px;
                width: 450px;
                height: 250px;
            }
            .story__heading{
                @include font(20,24);
            }
        }
        .two-story{
            .story{
                margin-bottom: 20px;
                @include last-y;
            }
            .story__thumb{
                width: 220px;
                height: 120px;
                margin-bottom: 8px;
            }
            .story__heading{
                @include font(18,22);
            }
        }
        .list-story{
            background: #FBFBFB;
            border: 1px solid #E7E7E7;
            padding-bottom: 15px;
            .story{
                border-bottom: 1px solid #F5F5F5;
                margin-bottom: 8px;
                padding-bottom: 8px;
                @include last-y;
            }
            .story__thumb{
                width: 220px;
                height: 120px;
                margin-bottom: 8px;
            }
            .story__heading{
                padding: 0 12px;
                @include font(14,18);
            }
        }
    }
}
