/*
* TABLE OF CONTENTS
*
* footer style
*/

// > footer style
.footer {
    background-color: darken($first-color,10%);
    position: relative;
    color: #fff;
    @include clearfix();
    .nav{
        background-color: darken($first-color,20%);
    }
    &__logo {
        position: absolute;
        top: 25px;
        left: 0;
        width: 200px;
        height: 66px;
        &:hover {
            opacity: .8;
        }
    }
    &__copyRight{
        padding-top: 20px;
        padding-bottom: 10px;
        float: right;
        @include font(14,22,$normal);
        text-align: right;
        a{
            color: $second-color;
        }
    }
}
