// ====================================
// Header's style
// ====================================

/*
* TABLE OF CONTENTS
*
* header style
*/

// > header style
.header {
    background-color: $first-color;
    color: #fff;
    span,
    a,{
        color: #fff;
    }
    .l-grid {
        @include clearfix();
        height: 115px;
    }
    &__logo {
        position: absolute;
        top: 24px;
        left: 0;
        width: 200px;
        height: 66px;
        &:hover {
            opacity: .8;
        }
    }
    &__search {
        position: absolute;
        top: 60px;
        right: 0px;
    }
    &__more {
        position: absolute;
        right: 0px;
        top: 0px;
        height: 36px;
        background-color: darken($first-color, 5%);
        text-align: right;
        @include font(12, 36, $normal);
        padding: 0 25px;
        border-radius: 0 0 8px 8px;
        .info,
        .login,
        .social,
        {
            display: inline-block;
            margin-left: 15px;
            padding-left: 15px;
            position: relative;
            &:before {
                content: '';
                width: 1px;
                height: 22px;
                position: absolute;
                left: 0;
                top: 7px;
                background-color: #fff;
            }
            &:first-child {
                padding-left: 0;
                margin-left: 0;
                &:before {
                    display: none;
                }
            }
            span,
            a {
                margin-left: 15px;
                @include first-x();
            }
        }
        a:hover{
            text-decoration: underline;
        }
    }
    &__search {
        position: absolute;
        right: 0px;
        top: 60px;
        height: 30px;
        color: #fff;
        text-align: right;
        padding-left: 24px;
        font-size: 0;
        > *{
            display: inline-block;
            font-size: 14px;
            vertical-align: top;
        }
        .key {
            &:before {
                content: '';
                width: 24px;
                height: 24px;
                position: absolute;
                left: 0;
                top: 3px;
                background-image: url('../img/desktop-sprite-'+$sprite-version+'.png');
                background-size: ($sprite-w + px) ($sprite-h + px);
                background-position: -0px -250px;
            }
            a{
                display: inline-block;
                height: 30px;
                line-height: 30px;
                padding: 0 20px;
                border-radius: 8px;
                background-color: rgba(white, .1);
                margin-left: 8px;
            }
            a:hover,
            .is-active{
                color: $second-color;
                background-color: rgba(white, .3);
            }
        }
        .search{
            margin-left: 15px;
            height: 30px;
            width: 230px;
            background: #FFFFFF;
            border-radius: 4px;
            overflow: hidden;
            color: #7f7f7f;
            >*{
                float: left;
            }
            button{
                width: 30px;
                height: 30px;
                border: none;
                padding: 0;
                background-color: transparent;
            }
            input{
                border: none;
                width: 200px;
                height: 30px;
                padding-left: 10px;
            }
        }
    }
}