// > box heading
.box-heading {
    .main {
        @include font(24, 32);
        i {
            vertical-align: top;
            margin-right: 10px;
        }
        span {
            display: inline-block;
        }
    }
    .sub {
        >* {
            @include font(14, 14, $normal);
            display: inline-block;
            padding-left: 10px;
            margin-left: 10px;
            border-left: 1px solid #E7E7E7;
            @include first-x;
        }
    }
}


// > abf
.abf {
    .wrap-border-600 {
        height: 550px;
    }
    .col-564 {
        float: left;
        margin-right: 16px;
    }
    .col-220 {
        float: left;
    }
    .col-300 {
        float: right;
    }
    .col-800 {
        float: left;
    }
    .first-story {
        .story__thumb {
            margin-bottom: 10px;
            width: 100%;
            height: 320px;
        }
        .story__heading {
            padding: 0 16px;
            margin-bottom: 5px;
            .cate {
                margin-top: 12px;
            }
            .text {
                @include font(36, 44, $normal);
            }
        }
        .story__meta {
            padding: 0 16px;
            margin-bottom: 5px;
        }
        .story__summary {
            display: none;
            padding: 0 16px;
        }
        .story__refer {
            margin-left: 16px;
            margin-right: 16px;
            a {
                @include font(14, 18);
            }
        }
    }
    .rank-2-stories {
        @include clearfix();
        padding: 20px 16px 10px 16px;
        .story {
            width: 255px;
            float: left;
            margin-bottom: 14px;
            vertical-align: top;
            &:nth-child(2n) {
                float: right;
            }
            &:nth-child(2n+1) {
                clear: left;
            }
        }
        .story__thumb {
            display: none;
            width: 40px;
            height: 40px;
            @include runaround(left, 2, 16, 0, 0);
        }
        .story__heading {
            @include font(14, 18);
            .cate {}
            .text {}
        }
    }
    .rank-2-stories-big {
        @include clearfix();
        .story {
            background-color: #fff;
            border: 1px solid #ddd;
            width: 390px;
            margin-left: 16px;
            float: left;
            &:first-child {
                margin-left: 0;
            }
        }
        .story__thumb{
            height: 220px;
        }
        .story__heading {
            padding: 16px;
            padding-bottom: 0;
        }
        .story__meta {
            padding: 16px;
            padding-bottom: 0;
        }
    }
    .rank-3-stories {
        @include clearfix();
        margin-top: 16px;
        height: 200px;
        background-color: #fff;
        border: 1px solid #ddd;
        padding: 15px;
        .story {
            width: 180px;
            float: left;
            margin-left: 16px;
            &:first-child {
                margin-left: 0;
            }
        }
        .story__thumb {
            height: 105px;
        }
        .story__heading {
            margin-top: 10px;
            @include font(14, 18);
            max-height: 36px;
            overflow: hidden;
        }
    }
    .list-story {
        height: 550px;
        overflow: hidden;
        background: #fff;
        border: 1px solid #E7E7E7;
        padding-bottom: 20px;
        .story {
            border-bottom: 1px solid #F5F5F5;
            margin-bottom: 8px;
            padding-bottom: 8px;
            @include last-y;
        }
        .story__thumb {
            width: 220px;
            height: 120px;
            margin-bottom: 8px;
        }
        .story__heading {
            padding: 0 12px;
            @include font(14, 18);
        }
    }
    .stories-with-thumb {
        .story {
            border-bottom: none;
            padding-bottom: 20px;
        }
    }
}

// > utilities
.utilities {
    border: 1px solid #ddd;
    background-color: #fff;
    margin-top: 16px;
    height: 200px;
    padding: 15px 10px;
    .tab {
        a {
            @include font(14, 20, $normal);
            color: #7c7c7c;
            display: inline-block;
            margin-left: 10px;
            &:first-child {
                margin-left: 0;
            }
        }
        .is-active {
            font-weight: bold;
            color: #000;
        }
    }
    .pannels {}
    .pannel {}
    .weather {
        @include clearfix();
        padding-top: 20px;
        .city-name {
            @include font(14, 20);
            float: left;
        }
        select {
            font-size: 14px;
            width: 130px;
            height: 26px;
            border: 1px solid #ddd;
            background-color: #fff;
            float: right;
        }
        .weather__info {
            p {
                width: 30%;
                float: left;
                text-align: center;
                padding-top: 20px;
                &:first-child {}
            }
            .title {
                font-size: 12px;
            }
            .info {
                font-size: 24px;
            }
        }
    }
}

// > timeline-stories
.timeline-stories {
    background: #FFFFFF;
    border: 1px solid #E7E7E7;
    padding: 20px 0 0;
    .story {
        @include first-y;
        padding-left: 16px;
        padding-right: 16px;
        padding-top: 20px;
        padding-bottom: 20px;
        border-top: 1px solid #E7E7E7;
    }
    .story--focus {
        background-color: #FFFEEB;
    }
    .story__heading {
        .cate {
            margin-top: 4px;
        }
        .text {
            @include font(18, 22);
        }
    }
    .story__meta {
        margin-bottom: 10px;
    }
    .story__thumb {
        width: 180px;
        height: 100px;
        @include runaround(left, 4, 16, 5, 0)
    }
}

.hasnt-read {
    height: 180px;
    background-color: #E7E7E7;
    overflow: hidden;
    padding-top: 10px;
    position: relative;
    .box-heading {
        padding-left: 16px;
    }
    .wrap {
        margin-top: 10px;
    }
    .story {
        padding-top: 0;
        padding-bottom: 0;
    }
    .user-controls {
        a {
            display: block;
            width: 24px;
            height: 24px;
            position: absolute;
            text-indent: -999em;
            top: 16px;
            background-image: url('../img/desktop-sprite-'+$sprite-version+'.png');
            background-size: ($sprite-w + px) ($sprite-h + px);
            &:hover {
                opacity: .7;
            }
        }
        .btn-prev {
            a {
                right: 48px;
                background-position: 0px -1650px;
                &:hover {
                    background-position: 0px -1700px;
                }
            }
        }
        .btn-next {
            a {
                right: 16px;
                background-position: -50px -1650px;
                &:hover {
                    background-position: -50px -1700px;
                }
            }
        }
        .more {
            position: absolute;
            bottom: 0;
            right: 10px;
            a {
                width: 24px;
                height: 24px;
                background-image: url('../img/desktop-sprite-'+$sprite-version+'.png');
                background-size: ($sprite-w + px) ($sprite-h + px);
                display: block;
                background-position: -50px -1650px;
            }
        }
    }
}

// > timeline-stories-02
.timeline-stories-02 {
    background: #FFFFFF;
    border: 1px solid #E7E7E7;
    padding: 10px 0 0;
    @include clearfix();
    .box-heading{
        padding-left: 16px;
    }
    .story {
        @include first-y;
        padding-left: 16px;
        padding-right: 16px;
        padding-top: 11px;
        &:first-child{
            padding-top: 10px;
        }
    }
    .story__thumb {
        width: 265px;
        height: 150px;
        @include runaround(left,
        5,
        15,
        0,
        0);
    }
    .story__heading {
        .cate {
            margin-top: 4px;
        }
        .text {
            @include font(20,
            24,
            600);
        }
    }
    .story__meta {
        margin-bottom: 10px;
    }
    .story__summary {
        @include font(16,20,600);
        max-height: 50px;
        overflow: hidden;
        border-top: 1px solid #ddd;
        padding-top: 10px;
        margin-top: 10px;
    }
    .story-full-width{
        padding-top: 16px;
        .story__thumb{
            margin-left: -17px;
            width: 622px;
            height: 350px;
            float: none;
            margin-top: 0;
            @include clearfix();
        }
        .story__heading{
            @include font(24,28,600);
            padding-top: 16px;
        }
        .story__meta{}
        .story__summary{
            @include font(14,18,$normal);
            border-top: none;
            margin-top: 0;
            max-height: none;
        }
    }
}

// > timeline-stories-03
.timeline-stories-03 {
    @include clearfix();
    .box-heading{
        padding-left: 16px;
    }
    .wrap{
        font-size: 0;
    }
    .story{
        border: 1px solid #ddd;
        background: #FFFFFF;
        margin-top: 16px;
        .story__thumb {
            height: 220px;
            img{
                width: 100%;
            }
        }
        .story__heading{
            padding: 16px 16px 0;
            @include font(20,24,600);
            max-height: 64px;
            overflow: hidden;
        }
        .story__meta,
        {
            padding: 5px 16px 0;
        }
        .story__summary {
            @include font(14,18,$normal);
            max-height: 46px;
            overflow: hidden;
            padding: 10px 16px 0;
            margin-top: 10px;
            display: none;
        }
        .story__refer{
            padding: 16px 16px 5px;
            margin-top: 20px;
            @include font(16,20,600);
        }
    }
    .story--normal{
        width: 392px;
        max-height: 400px;
        margin-left: 16px;
        display: inline-block;
        vertical-align: top;
        &:nth-child(2n + 1){
            margin-left: 0;
        }

    }
    .story-full-width{
        .story__heading{
            @include font(24,28,600);
            max-height: inherit;
            overflow: inherit;
        }
        .story__thumb{
            height: 450px;
        }
    }
}

// > box-rank-1
