    .box-heading {}
    .first-story {
        .story {
            margin-bottom: 10px;
            &:first-child {
                padding-top: 8px;
            }
        }
        .story__thumb {
            width: 100%;
            height: 250px;
            margin-bottom: 10px;
        }
        .story__heading {
            @include font(18,
            22);
            margin-bottom: 5px;
            .cate {
                margin-top: 2px;
            }
        }
        .story__meta {}
        .story__summary {
            margin-top: 10px;
        }
    }
    .list-stories {
        .story {
            padding-top: 14px;
            &:first-child {
                padding-top: 0;
            }
        }
        .story__thumb {
            width: 180px;
            height: 100px;
            @include runaround(left,
            2,
            16,
            0,
            0);
        }
        .story__heading {
            @include font(16,
            20);
            font-weight: 600;
            margin-bottom: 5px;
            .cate {
                margin-top: 2px;
            }
        }
        .story__meta {}
        .story__summary {
            margin-top: 10px;
        }
