}

// > box-rank-2
// piority-2, small column
.box-rank-2 {
    .box-heading {
        padding: 10px 16px 0px 16px;
    }
    background: #FFFFFF;
    border: 1px solid #E7E7E7;
    .story {
        padding: 16px;
        border-bottom: 1px solid #e7e7e7;
        &:first-child {
            padding-top: 8px;
        }
        &:last-child {
            border-bottom: none;
            padding-bottom: 20px;
        }
    }
    .story__thumb {
        width: 100%;
        height: 150px;
        margin-bottom: 10px;
    }
    .story__heading {
        @include font(16,
        20);
        font-weight: 600;
        margin-bottom: 5px;
        .cate {
            margin-top: 2px;
        }
    }
    .story__meta {}
    .story__summary {
        margin-top: 10px;
    }
    &.first-story-thumb {
        .story:first-child {
            .story__heading {
                @include font(20,
                24);
            }
        }
    }
    &.thumb-full-width {
        .story:first-child {
            .story__heading {
                @include font(20,
                24);
            }
        }
        .story__thumb {
            width: 300px;
            height: 170px;
            margin-left: -16px;
        }
    }
    &.theme-red{
        background-color: #E54B4B;
        p a,
        span,
        time,
        {
            color: #fff !important;
        }
        .box-heading {
            .main {
                padding: 10px 0 5px;
                @include font(24,24,600);
            }
            &:hover a {
                opacity: .7;
            }
        }

    }
}

// > special-box
.special-box {
    min-height: 590px;
    padding: 25px 0 30px;
    background-color: $second-color;
    .story__meta {
        a,
        span,
        time {
            color: #000;
        }
    }
    &__heading {
        text-align: center;
        .main {
            @include font(24,
            30,
            $normal);
            text-transform: uppercase;
            a:hover {
                span {
                    text-decoration: underline;
                    color: #fff;
                }
            }
        }
        .sub {
            >* {
                @include font(14,
                26,
                $normal);
                display: inline-block;
                margin-left: 10px;
            }
            h3:hover,
            .is-active {
                border-bottom: 1px solid #000;
            }
        }
    }
    &__content {
        position: relative;
        padding-top: 20px;
        @include clearfix();
        .col-300 {
            float: right;
        }
    }
    .first-story {
        margin-bottom: 16px;
        .story {}
        .story__thumb {
            position: absolute;
            width: 800px;
            height: 450px;
            left: 0;
            top: 25px;
        }
        .story__heading {
            @include font(24,
            30,
            $normal);
            margin-bottom: 5px;
            .cate {
                margin-top: 7px;
            }
        }
        .story__meta {
            margin-bottom: 10px;
            >* {
                &:after {
                    background-color: #000;
                }
            }
        }
        .story__summary {}
    }
    .more-stories {
        padding-top: 16px;
        .story {
            margin-bottom: 16px;
            @include last-y;
        }
        .story__thumb {
            width: 135px;
            height: 75px;
            @include runaround(left,
            3,
            16,
            0,
            0);
        }
        .story__heading {
            @include font(14,
            18);
        }
    }
    &.theme-black {
        background-color: #2B3035;
        color: #fff;
        .story:hover {
            .text {
                color: #fff;
            }
            .comment:hover i {
                background-position: 0 -2100px;
            }
        }
        .story__heading {
            &:hover .text {
                color: #fff;
                text-decoration: underline;
            }
        }
        .story__meta {
            a,
            span,
            time {
                color: #fff;
            }
        }
        .special-box__heading {
            .main {}
            .sub {
                h3:hover,
                .is-active {
                    border-bottom: 1px solid #fff;
                }
            }
        }
        a {
            color: #fff;
        }
        .more-stories {
            border-top: 1px solid #fff;
        }
    }
}

// > special-box-02
.special-box-02 {
    padding: 25px 0 30px;
    background-color: $first-color;
    height: 526px;
    overflow: hidden;
    time,
    span,
    a {
        color: #f1f1f1;
    }
    a:hover {
        color: #fff;
    }
    &__heading {
        text-align: center;
        .main {
            @include font(24,
            30,
            $normal);
            text-transform: uppercase;
            >*:hover,
            {
                border-bottom: 1px solid #fff;
            }
            a:hover span {
                color: #fff;
            }
        }
        .sub {
            >* {
                @include font(14,
                26,
                $normal);
                display: inline-block;
                margin-left: 10px;
            }
            >*:hover,
            .is-active {
                border-bottom: 1px solid #fff;
            }
        }
    }
    &__content {
        position: relative;
        padding-top: 20px;
        @include clearfix();
    }
    &__option {
        padding: 30px;
        @include clearfix();
        .thumb {
            width: 160px;
            height: 160px;
            border: 3px solid #fff;
            float: left;
            overflow: hidden;
            border-radius: 50%;
            img {
                width: 100%;
                height: 100%;
            }
        }
        .quote {
            margin-left: 260px;
            margin-right: 70px;
            font-style: italic;
            font-size: 24px;
            color: #fff;
            padding-top: 20px;
            position: relative;
            &:before,
            &:after,
            {
                width: 50px;
                height: 50px;
                content: '';
                position: absolute;
                background-image: url('../img/desktop-sprite-'+$sprite-version+'.png');
                background-size: ($sprite-w + px) ($sprite-h + px);
                display: inline-block;
                vertical-align: middle;
            }
            &:before {
                top: -5px;
                left: -55px;
                background-position: 0 -1750px;
            }
            &:after {
                bottom: -25px;
                right: -25px;
                background-position: -50px -1750px;
            }
        }
    }
    .story:hover {
        .text {
            color: #fff;
        }
        .comment:hover i {
            background-position: 0 -2100px;
        }
    }
    .story {
        width: 370px;
        overflow: hidden;
        float: left;
        margin-left: 3px;
        text-align: center;
        &:first-child {
            margin-left: 0;
        }
        &:hover {
            .topic {
                color: #000;
            }
            .number,
            .text,
            a {
                color: #fff;
            }
        }
    }
    .story__thumb {
        width: 370px;
        height: 210px;
    }
    .story__heading {
        @include font(24,
        30,
        $normal);
        margin: 0px 10px 0;
        .topic {
            @include font(12,
            16,
            $normal);
            margin-top: 20px;
            display: inline-block;
            background-color: $second-color;
            color: #000;
            margin-bottom: 10px;
        }
        .cate {
            margin-top: 5px;
            display: inline-block;
        }
        .text {
            margin-top: 15px;
        }
        &:hover .text {
            color: #fff;
            text-decoration: underline;
        }
    }
    .story__meta {
        margin-top: 10px;
        >* {
            float: none;
            display: inline-block;
            &:after {
                // background-color: #000;
            }
        }
    }
    .story__summary {
        max-height: 72px;
        margin: 10px;
        margin-bottom: 0;
    }
    &.theme-gardian {
        padding: 25px 0 3px;
        background-image: linear-gradient(89deg, #0C4C9B 0, #DE0202 100%);
        .special-box__heading {
            .main {}
            .sub {
                .is-active {}
            }
        }
        .story {
            background-color: #000;
            padding-bottom: 20px;
            height: 420px;
            display: block;
        }
        .story__meta {
            a,
            span,
            time {
                color: #fff;
            }
        }
        .story__summary {
            max-height: 72px;
            overflow: hidden;
        }
        .user-controls {
            a {
                display: block;
                width: 150px;
                height: 420px;
                position: absolute;
                top: 20px;
                text-indent: -999em;
                background-color: #000;
                &:after {
                    content: '';
                    width: 50px;
                    height: 50px;
                    background-image: url('../img/desktop-sprite-'+$sprite-version+'.png');
                    background-size: ($sprite-w + px) ($sprite-h + px);
                    background-position: 0 -1800px;
                    position: absolute;
                    top: calc(50% - 25px);
                    left: calc(50% - 25px);
                }
                &:hover {
                    opacity: .7;
                }
            }
            .btn-prev {
                a {
                    left: -150px;
                }
            }
            .btn-next {
                a {
                    right: -147px;
                    &:after {
                        background-position: -50px -1800px;
                    }
                }
            }
        }
    }
    &.has-quote {
        height: 745px;
    }
}


// trending
.tag-box {
    padding: 16px;
    background-color: #fff;
    border: 1px solid #ddd;
    .box-heading {}
    .box-content {
        margin-top: 10px;
    } // keyword size , example : .w15{font-size : 250%}
    @include clearfix();
    $i: 0;
    @while $i < 20 {
        .w#{$i + 1} {
            font-size: 10% * $i + 100%;
        }
        $i: $i+1;
    } // keyword color 
    .w20 {
        color: #0cf;
    }
    .w19 {
        color: #0cf;
    }
    .w18 {
        color: #0cf;
    }
    .w17 {
        color: #3399DD;
    }
    .w16 {
        color: #33CDDD;
    }
