// > list with number
.list-number {
    counter-reset: myIndex;
    background: #FFF;
    border: 1px solid #E7E7E7;
    .box-heading {
        padding: 16px;
        padding-bottom: 10px;
    }
    &__content {}
    .story {
        position: relative;
        border-bottom: 1px solid #e7e7e7;
        padding-top: 5px;
        padding-bottom: 15px;
        &:last-child {
            border-bottom: none;
        }
    }
    .story:before {
        counter-increment: myIndex;
        content: counter(myIndex) "  ";
        @include font(48,
        48,
        $normal);
        width: 55px;
        text-align: center;
        position: absolute;
        top: 5px;
        left: 0;
    }
    .story__heading {
        padding: 15px 5px 0 55px;
        .cate {
            @include font(12,
            16,
            $normal);
            vertical-align: top;
            margin-top: 3px;
            padding: 2px 5px 0;
        }
        .text {
            @include font(18,
            22,
            $normal);
        }
    }
    .story__meta {
        padding-left: 55px;
        .author,
        time {
            display: none;
        }
    }
    &.image-bg {
        .story {
            overflow: hidden;
            color: #fff;
            a,
            span {
                color: #fff;
            }
            .comment {
                i {
                    background-position: -0px -2182px;
                }
                span {
                    color: $second-color;
                }
            }
        }
        .story:before {
            z-index: 10;
        }
        .story__thumb {
            position: absolute;
            top: -5px;
            &:after {
                content: '';
                position: absolute;
                left: 0;
                right: 0;
                top: 0;
                bottom: 0;
                background-color: rgba(black, .7);
            }
        }
        .story__heading {
            position: relative;
            z-index: 10;
        }
    }
}


