.l-grid {
    position: relative;
    width: 1116px;
    margin: 0 auto;
    @include clearfix;
}

.col-160 {
    width: 160px;
}

.col-220 {
    width: 220px;
}

.col-300 {
    width: 300px;
}

.col-392 {
    width: 392px;
}

.col-450 {
    width: 450px;
}

.col-484 {
    width: 484px;
}

.col-564 {
    width: 564px;
}

.col-622 {
    width: 622px;
}

.col-654 {
    width: 654px;
}

.col-768 {
    width: 768px;
}

.col-800 {
    width: 800px;
}

.wrapper {
    @include clearfix;
    position: relative;
}

.header {}

.body {
    @include clearfix();
    background-color: #f1f1f1;
    padding-bottom: 16px;
}

.abf {
    @include clearfix();
    .wrap-border-600 {
        background: #FFFFFF;
        border: 1px solid #E7E7E7;
        overflow: hidden;
    }
}

.main-content {
    @include clearfix();
    > [class^="col-"]{
        margin-left: 16px;
        float: left;
        &:first-child{
            margin-left: 0;
        }
    }
    .col-160{
        float: left;
    }
    .col-300{
        > * {
            margin-top: 16px;
            &:first-child{
                margin-top: 0;
            }
        }
    }
    .col-484{
        > * {
            margin-top: 16px;
            &:first-child{
                margin-top: 0;
            }
        }
    }
    .col-800{
        float: left;
    }
}


.footer {}

