// ====================================
// Story's style
// ====================================

/*
* TABLE OF CONTENTS
*
* no image in article
* normal story
* story type
*/

// > no image in article
.no-fig {
    .story__thumb a {
        width: 100%;
        height: 100%;
        display: block;
        background: #f7f7f7;
        text-align: center;
        text-transform: uppercase;
        color: #bbb;
        font-size: 10px;
        font-size: 1.0rem;
        &:after {
            content: 'Khng c nh' !important;
            display: block;
            padding-top: 28%;
            width: 100% !important;
            height: 100% !important;
            background-image: none !important;
            background-color: transparent !important;
            top: 0 !important;
            left: 0 !important;
            right: 0 !important;
            bottom: 0 !important;
        }
        img {
            display: none;
        }
    }
}

// > normal story
.story {
    @include clearfix;
    &__thumb {
        overflow: hidden;
        background-color: #eee;
        img {
            width: 100%;
            height: auto;
            transition: transform .5s ease-out .1s;
            text-indent: -9999px;
            transform: translate3d(0, 0, 0);
        }
        &:hover {
            overflow: hidden;
            img {
                opacity: .8;
                // -webkit-transform: scale($rate-zoom);
                // /* Safari and Chrome */
                // -moz-transform: scale($rate-zoom);
                // /* Firefox */
                // -ms-transform: scale($rate-zoom);
                // /* IE 9 */
                // -o-transform: scale($rate-zoom);
                // /* Opera */
                // transform: scale($rate-zoom);
            }
        }
    }
    &__heading {
        a {
            display: block;
        }
        a:visited {
            // color: #999;
        }
        .cate {
            display: inline-block;
            font-weight: normal;
            vertical-align: top;
            background-color: #555;
            color: #fff;
            @include font(11, 14, $normal);
            padding: 1px 5px;
            margin-right: 5px;
            // text-transform: uppercase;
        }
    }
    &__meta {
        color: $grey-color;
        font-size: 0;
        height: 20px;
        line-height: 20px;
        vertical-align: middle;
        overflow: hidden;
        @include clearfix();
        font-size: 0;
        >* {
            margin-right: 8px;
            padding-right: 8px;
            float: left;
            position: relative;
            @include font( 12, 22, $normal);
            &:after {
                content: '';
                width: 1px;
                height: 12px;
                top: 4px;
                right: 0;
                position: absolute;
                background-color: $grey-color;
            }
            &:last-child {
                padding-right: 0;
                margin-right: 0;
                &:after {
                    display: none;
                }
            }
        }
        a {
            vertical-align: top;
            display: inline-block;
            color: $grey-color;
            &:hover {
                color: $first-color;
            }
        }
        .author {
            a {
                color: #DE0202;
                font-weight: bold;
                margin-left: 3px;
            }
        }
        .comment {
            i {
                vertical-align: top;
                margin-top: 2px;
            }
            &:hover{
                i{
                    background-position: -16px -2100px;
                }
            }
        }
    }
    &__tag {
        i {}
        a {}
        span {}
    }
    &__summary {
        @include font( 14, 18, $normal);
    }
    .story__refer {
        border-top: 1px solid #E7E7E7;
        margin-top: 10px;
        padding-top: 10px;
        a {
            display: block;
            margin-bottom: 10px;
            &.story--video,
            &.story--photo,
            &.story--live,
            {
                &:after {
                    background-image: url('../img/desktop-sprite-'+$sprite-version+'.png');
                    background-size: ($sprite-w + px) ($sprite-h + px);
                    content: '';
                    width: 16px;
                    height: 16px;
                    display: inline-block;
                    vertical-align: middle;
                    margin: 0 5px;
                }
            }

            &.story--photo {

                &:after {
                    background-position: -50px -2100px;
                }
                &:hover {
                    &:after {
                        background-position: -82px -2100px;
                    }
                }
            }
            &.story--video {
                &:after {
                    background-position: -50px -2132px;
                }
                &:hover {
                    &:after {
                        background-position: -82px -2132px;
                    }
                }
            }
            &.story--live {
                &:after {
                    width: 32px;
                    background-position: -50px -2150px; 
                    // background-color: rgba(255, 0, 0, .7);
                    // -webkit-animation: animation-blink 1s infinite;
                    // -moz-animation: animation-blink 1s infinite;
                    // -o-animation: animation-blink 1s infinite;
                    // animation: animation-blink 1s infinite;
                }
                &:hover {
                    &:after {
                        background-position: -50px -2150px;
                    }
                }
            }
        }
    }
    &:hover {
        .story__heading {
            a {
                // color: $first-color;
            }
        }
    }
    &:focus {
        background-color: red;
    }
}


/* story type */

.story--video,
.story--photo,
.story--live,
{
    .story__heading {
        >a:after {
            background-image: url('../img/desktop-sprite-'+$sprite-version+'.png');
            background-size: ($sprite-w + px) ($sprite-h + px);
            content: '';
            width: 16px;
            height: 16px;
            display: inline-block;
            vertical-align: middle;
            margin: 0 5px;
        }
    }
}

.story--photo {
    .story__heading {
        a:after {
            background-position: -50px -2100px;
        }
    }
    &:hover {
        .story__heading {
            a:after {
                background-position: -82px -2100px;
            }
        }
    }
}

.story--video {
    .story__heading {
        a:after {
            background-position: -50px -2132px;
        }
    }
    &:hover {
        .story__heading {
            a:after {
                background-position: -82px -2132px;
            }
        }
    }
}

.story--live {
    .story__heading {
        a:after {
            width: 32px;
            background-position: -50px -2150px; 
            // background-color: rgba(255, 0, 0, .7);
            // -webkit-animation: animation-blink 1s infinite;
            // -moz-animation: animation-blink 1s infinite;
            // -o-animation: animation-blink 1s infinite;
            // animation: animation-blink 1s infinite;
        }
    }
    &:hover {
        .story__heading {
            a:after {
                background-position: -50px -2150px;
            }
        }
    }
}
